import Vue from 'vue'
import Router from 'vue-router'
import home from '@/sections/home'
import doc from '@/sections/doc'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/doc',
      name: 'doc',
      component: doc
    }
  ]
})



