<template lang="html">
  <div class="con-componente">
    <h3>{{title}}</h3>
    <p>{{parrafo}}</p>
    <div class="ejemplo">
      <slot name="ejemplo">
      </slot>

    </div>
    <pre>
      <code class="html">
      <slot name="code">
      </slot>
    </code>
    </pre>

  </div>
</template>

<script>
export default {
  props:[
    "title",
    "parrafo"
  ]
}
</script>

<style lang="css" scoped>
.con-componente {
    padding: 20px;
}
.con-componente h3{
  font-weight: lighter;
  padding: 10px;
  padding-left: 0px;
  font-size: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.con-componente p {
  padding: 15px;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.7);
}
.ejemplo {
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  display: flex;
  align-items: center;
  justify-content: center;
}
  pre {
    width: 100%;
    position: relative;
    padding: 0px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .html{
    display: block;
    background: rgb(240, 240, 240);
    width: 100%;
    height: auto;
    padding: 10px;
    border-radius: 5px;
    font-size: 15px;
  }
  .html span.uno{
    color: rgb(var(--dos));
  }
  .html span.dos{
    color: rgb(var(--uno));
  }
  .html span.tres{
    color: rgb(1, 124, 162);
  }
</style>



